import React from "react";
import { MdCheckCircle, MdClose } from "react-icons/md";
import { Auth } from "../../../actions/auth";
import { candidateExamStoreInterface } from "../../../actions/candidate_exam";
import { questionTypesStoreInterface } from "../../../actions/questionTypes";
import { System } from "../../../actions/system";
import Loading from "../../../components/Loading/Loading";
import { course } from "../../../interfaces/course";
import {
  QuestionInterface,
  QuestionLanguage,
} from "../../../interfaces/question";
import { ExamAnsweredQuestions, ExamPendingQuestions } from "../utils/utils";
import FindAnswer from "./FindAnswer";

interface QuestionsReviewProps {
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  setSelectedExamQuestion: (
    exam_id: string,
    question_id: string | null
  ) => void;
  submitQuestionAnswer: boolean;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  submitExam: () => void;
  selectedExam: course | null;
  setSubmitModal: (status: boolean) => void;
  selectedLanguage: QuestionLanguage;
}

const QuestionsReview = (props: QuestionsReviewProps) => {
  const AnsweredQuest: QuestionInterface[] = props.candidateExam.candidate_exam
    ? ExamAnsweredQuestions(
        props.candidateExam.question_answers,
        props.candidateExam.candidate_exam.questions,
        props.questionTypes
      )
    : [];
  const PendingQuest: QuestionInterface[] = props.candidateExam.candidate_exam
    ? ExamPendingQuestions(
        props.candidateExam.question_answers,
        props.candidateExam.candidate_exam.questions,
        props.questionTypes
      )
    : [];
  if (props.submitQuestionAnswer === true) {
    return (
      <div className="bg-white rounded mx-4 my-4">
        <div className="text-gray-600 mx-4">Saving changes, Please wait...</div>
        <Loading />
      </div>
    );
  }
  return (
    <div
      className={`pt-4 border-t border-gray-300 ${
        PendingQuest.length === 0 ? "bg-gray-100" : "bg-white"
      }`}
    >
      {props.candidateExam.candidate_exam &&
      AnsweredQuest.length !==
        props.candidateExam.candidate_exam.questions.length &&
      PendingQuest.length !== 0 ? (
        <div>
          <div className="grid md:grid-cols-2 h-full">
            <div className="md:col-span-2 h-full">
              <div className="mb-3 rounded-t bg-gray-200 h-full px-2 py-4 -mt-4 pl-4 flex flex-row justify-between">
                {/* <RiErrorWarningFill className="text-6xl text-gray-400" /> */}
                <div className="flex flex-col">
                  <div className="text-2xl text-gray-900 font-extrabold mb-2 text-left">
                    Submit incomplete questions
                  </div>
                  <div className="text-gray-700 text-sm text-left pb-2">
                    Please, make sure to complete the following remaining
                    questions which are not yet answered. Please, if you proceed
                    anyway, for these questions, you will not be marked!
                  </div>
                </div>
                <div className="mr-4">
                  <div className="bg-white hover:text-red-700 hover:bg-red-200 rounded-full p-1 h-10 w-10 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() => props.setSubmitModal(false)}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="md:col-span-2">
              <div className="">
                <div className="border-2 border-yellow-500 bg-yellow-50 px-2 py-3 rounded mx-4">
                  {/* <div className="text-xl text-center text-gray-700 font-bold">List of incomplete questions</div> */}
                  <div className="text-center mb-5 text-gray-600 font-bold">
                    Pick a question number you want to answer directly
                  </div>
                  <div className="flex justify-center items-center text-center">
                    <div className="flex flex-row items-center justify-center gap-4 w-full overflow-x-auto pb-2">
                      {props.candidateExam.candidate_exam.questions.map(
                        (item, i) =>
                          PendingQuest.find(
                            (itm) => itm.question_id === item.question_id
                          )?.question_id !== undefined && (
                            <div
                              onClick={() => {
                                props.candidateExam.candidate_exam &&
                                  props.setSelectedExamQuestion(
                                    props.candidateExam.candidate_exam
                                      .exam_paper_id,
                                    item.question_id
                                  );
                                props.setSubmitModal(false);
                              }}
                              key={i + 1}
                              className="w-10 h-10 p-4 rounded-full bg-yellow-600 flex justify-center items-center font-bold text-2xl text-white cursor-pointer"
                            >
                              {i + 1}
                            </div>
                          )
                      )}
                    </div>
                  </div>
                </div>
                <div className="flex flex-row items-center justify-between my-2 gap-3 mx-4 mb-16">
                  <div
                    onClick={() => props.setSubmitModal(false)}
                    className="bg-gray-100 font-bold text-gray-800 flex items-center rounded px-3 py-2 cursor-pointer hover:bg-gray-200 text-base"
                  >
                    Back to edit
                  </div>
                  <div
                    onClick={() => props.submitExam()}
                    className="bg-blue-100 text-blue-700 font-bold flex items-center rounded px-3 py-2 cursor-pointer hover:bg-blue-300"
                  >
                    Submit anyway
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div className="px-4 pb-2">
          <div className="flex flex-row items-center justify-between mb-4">
            <div className="text-blue-700 font-bold">
              Hey{" "}
              <b>
                <u>{props.auth.user.fname}</u>
              </b>
              , All questions have been answered, are you sure do you want to
              submit the final answers?
            </div>
            <div className="bg-white hover:text-red-700 hover:bg-red-200 rounded-full p-1 h-10 w-10 flex items-center justify-center">
              <MdClose
                className="cursor-pointer font-extrabold text-3xl"
                onClick={() => props.setSubmitModal(false)}
              />
            </div>
          </div>
          <div className="flex flex-row items-center justify-between">
            <div
              onClick={() => props.setSubmitModal(false)}
              className="btn cursor-pointer bg-gray-200 hover:bg-gray-300 text-gray-800 flex flex-row gap-2"
            >
              <span className="">Back to edit</span>
            </div>
            <div
              onClick={() => {
                if (
                  window.confirm(
                    "Are you sure do you want submit your exam?"
                  ) === true
                ) {
                  props.submitExam();
                }
              }}
              className="btn cursor-pointer bg-blue-600 hover:bg-blue-800 text-white flex flex-row items-center gap-2 animate__animated animate__zoomIn"
            >
              <MdCheckCircle className="text-3xl" />
              <span className="">Submit exam</span>
            </div>
          </div>

          <div className="my-2">
            <div className="font-extrabold text-xl text-yellow-900 my-2 mt-4">
              Verify your answers
            </div>
            <div
              className="flex flex-col gap-3 overflow-y-auto"
              style={{ maxHeight: "100%" }}
            >
              {props.candidateExam.candidate_exam === null ? (
                <Loading />
              ) : (
                AnsweredQuest.length > 0 &&
                AnsweredQuest.map((item, i) => (
                  <div
                    key={i + 1}
                    className="bg-gray-200 hover:bg-white border border-white hover:border-blue-400 px-2 py-2 rounded  animate__animated animate__fadeIn animate__fast group"
                  >
                    <div className="grid md:grid-cols-12 w-full">
                      <div className="col-span-11 flex flex-row items-center gap-3">
                        <div
                          key={i + 1}
                          className="w-6 h-6 p-2 rounded-full bg-gray-600 flex justify-center items-center font-bold text-lg text-white cursor-pointer"
                        >
                          {i + 1}
                        </div>
                        <div className="text-sm w-full">
                          <div
                            className="font-bold text-gray-700 text-base"
                            dangerouslySetInnerHTML={{
                              __html: item.question.find(
                                (test) =>
                                  test.language === props.selectedLanguage
                              )!.description,
                            }}
                          ></div>
                          <div className="text-blue-700 font-bold text-sm w-full">
                            Answer:&nbsp;{" "}
                            {props.questionTypes.questionTypes !== null && (
                              <FindAnswer
                                selectedLanguage={props.selectedLanguage}
                                question={item}
                                answersList={
                                  props.candidateExam.question_answers
                                }
                                questionTypes={
                                  props.questionTypes.questionTypes
                                }
                                onClick={() => {
                                  props.candidateExam.candidate_exam &&
                                    props.setSelectedExamQuestion(
                                      props.candidateExam.candidate_exam
                                        .exam_paper_id,
                                      item.question_id
                                    );
                                  props.setSubmitModal(false);
                                }}
                                showCorrectAnswer={false}
                              />
                            )}
                          </div>
                        </div>
                      </div>
                      <div className="col-span-12 md:col-span-1 float-right flex items-center justify-end pt-2 md:pt-0">
                        <div
                          onClick={() => {
                            props.candidateExam.candidate_exam &&
                              props.setSelectedExamQuestion(
                                props.candidateExam.candidate_exam
                                  .exam_paper_id,
                                item.question_id
                              );
                            props.setSubmitModal(false);
                          }}
                          className="bg-blue-100 rounded px-2 py-1 text-center float-right cursor-pointer font-bold text-md text-blue-600 border border-blue-400 group-hover:bg-blue-600 group-hover:text-white group-hover:border-none flex items-center justify-center h-max md:ml-3"
                        >
                          Edit
                        </div>
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default QuestionsReview;
