import React, { useEffect } from "react";
import { QuestionAnswersStore } from "../../../../actions";
import {
  NumericalQuestionAnswerInterface,
  questionTypeElementsType,
} from "../../../../interfaces/question";

interface NumericalProps {
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null;
    answers: NumericalQuestionAnswerInterface[] | undefined;
  };
  answerQuestionFn: Function;
}

const Numerical = (props: NumericalProps) => {
  const checkAnswer = (item: NumericalQuestionAnswerInterface): boolean => {
    let res = false;
    if (props.available_answers.length > 0) {
      let check = props.available_answers.find(
        (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
      );
      if (check !== undefined) {
        let x = check.answer.find((itm) => itm.id === item.id);
        if (x !== undefined && x.value[0] !== "") {
          res = true;
        }
      }
    }
    return res;
  };
  const addAnswerItemFn = (
    newAnswers: { id: string; value: string[]; status: number[] }[]
  ) => {
    if (props.selectedQuestionAnswer.question_id !== null) {
      props.answerQuestionFn({
        question_id: props.selectedQuestionAnswer.question_id,
        status: false,
        answer: [
          {
            id: newAnswers[0].id,
            value: newAnswers[0].value,
            status: newAnswers[0].status,
          },
        ],
      });
    }
  };
  const addAnswer = (
    item: { id: string; value: string; status: number[] },
    question_id: string | null
  ) => {
    let oldAnswers = props.available_answers.find(
      (itm) => itm.question_id === question_id
    );
    if (oldAnswers !== undefined) {
      // Check type of question
      addAnswerItemFn([
        {
          id: oldAnswers.answer[0].id,
          value: [item.value],
          status: oldAnswers.answer[0].status,
        },
      ]);
    }
  };
  const answeredValue = () => {
    let answer = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    return answer !== undefined
      ? answer.answer.length !== 0 && answer.answer[0].value[0]
      : "";
  };

  useEffect(() => {
    let test = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    if (
      test === undefined ||
      (test !== undefined && test.answer.length === 0)
    ) {
      if (
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer !== undefined &&
        props.selectedQuestionAnswer.question_id !== null
      ) {
        props.answerQuestionFn({
          question_id: props.selectedQuestionAnswer.question_id,
          status: true,
          answer: [
            {
              id: props.selectedQuestionAnswer.answers[0].id,
              value: [""],
              status: props.selectedQuestionAnswer.answers[0].status,
            },
          ],
        });
      }
    }
  }, [props]);
  return (
    <div className="">
      {props.selectedQuestionAnswer !== null &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer.answers.map((item, i) => (
          <div
            key={i + 1}
            className={`-mx-2 -my-2 flex flex-col justify-left rounded cursor-pointer`}
          >
            <input
              onChange={(e) => {
                addAnswer(
                  {
                    id: item.id,
                    value: e.target.value,
                    status: item.status,
                  },
                  props.selectedQuestionAnswer.question_id
                );
              }}
              type="text"
              value={answeredValue()}
              className={`px-3 py-2 rounded focus:outline-none focus:border-none ${
                checkAnswer(item) === true
                  ? "border-2 text-blue-600 font-bold border-blue-600"
                  : "border text-gray-700 border-gray-500"
              }`}
            />
          </div>
        ))}
    </div>
  );
};

export default Numerical;
